@media (max-width: 1024px){
    .text__welcome{
        p{
            font-size: 48px;
        }
    }
    .project-list{
        grid-template-columns: 1fr;
        .project-list__block{
            .block__img{
                img{
                    width: 85%;
                    border-top: 1.5rem solid #DBECFF;
                    border-left: 3rem solid #DBECFF;
                    border-bottom: 1.5rem solid #DBECFF;
                    border-right: 3rem solid #DBECFF;       
                }
            }
        }
    }
}

@media (max-width: 512px){
    .text__welcome{
        p{
            font-size: 24px;
        }
    }
    .project-list{
        margin: 0;
        grid-template-columns: 1fr;
        .project-list__block{
            margin: 20px 0px;
            .block__img{
                img{
                    width: 85%;
                    border-top: 1rem solid #DBECFF;
                    border-left: 1.5rem solid #DBECFF;
                    border-bottom: 1rem solid #DBECFF;
                    border-right: 1.5rem solid #DBECFF;       
                }
            }
            .block__text{
                margin-left: 10%;
            }
        }
    }
    .intro{
        .project__description{
            h1{
                font-size: 32px;
            }
            p{
                font-size: 16px;
                line-height: 32px;
            }
        }
    }
    .before-after{
        .container{
            h1{
                font-size: 32px;
            }
        }
        .image-list{
            grid-template-columns: none;
            grid-template-rows: repeat(2, 1fr);
            grid-row-gap: 40px;
        }
    }

    .results{
        .container{
            h1{
                font-size: 32px;
            }
            li{
                font-size: 16px;
            }
        }
    }

    footer{
        grid-template-columns: none;
        grid-template-rows: 1fr;
        a{
            border-bottom: 1px solid #dde5ff;
        }
    } 
}

